import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';

import ChartistGraph from 'react-chartist';



// const client = new WebSocket('wss://devetlflow.startv.com/ws/etlflow/eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjoibWludF9hZG1pbiIsICJyb2xlIjoiYWRtaW4ifQ.gwiUv0vss-3gX5rkPOGTdJ_81n5N3W7Hv1yeGsJj5g8')

var ws_url = new URL("/ws/etlflow" , window.location.href)
ws_url.protocol = ws_url.protocol.replace("http","ws")

class MemoryInfo extends React.Component {
  constructor(props){
    super(props); 
    const GRAPHQL_URL = '/ws/etlflow'
    this.client = new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
    // this.client = new WebSocket('wss://devetlflow.startv.com/ws/etlflow/eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjoibWludF9hZG1pbiIsICJyb2xlIjoiYWRtaW4ifQ.gwiUv0vss-3gX5rkPOGTdJ_81n5N3W7Hv1yeGsJj5g8')

    this.state  = { 
      messages:''
    }
  }

  componentDidMount() {
    this.client.onopen = () => {
    // on connecting, do nothing but log it to the console
    console.log(' ws connected')
    }   

    this.client.onmessage = evt => {
    // listen to data sent from the websocket server
    const message = evt.data
    this.addMessage(message)
    }
  }

  componentWillUnmount() {
    this.client.close()
  }

  addMessage = message =>
    this.setState(prevState => ({
      messages: message
  }))


  render(){
    var seriesVals = [];
    var labelsVals = [];

    this.state.messages.split("</br>").map( data => (
      labelsVals.push(data.split(":")[0].replace("\n","")),
      seriesVals.push(data.split(":")[1])
    ))
    
    var data = {
      labels: labelsVals,
      series: [seriesVals]
    };

    var options = {
      // axisX: {
      //   labelInterpolationFnc: function(value, index) {
      //     return index % 2 === 0 ? value : null;
      //   }
      // }
    };

    var type = 'Line'

    return (
    <div class="container">
    <div>
            <ul class="list-group ">
            {this.state.messages.split("</br>").map( data => (
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>{data.split(":")[0]}</b>
              <span class="badge badge-secondary badge-pill">{data.split(":")[1]}</span>
            </li> ))}
            </ul>
     </div> 
     <br/>
     <div class="container-fluid">
        <ChartistGraph data={data} options={options} type={type} />
     </div> 
     </div>
    ); 
  }        
}

export default MemoryInfo;
